@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap");
h1, h2, h3, h4 {
  font-weight: 700;
}

h5, h6 {
  font-weight: 600;
}

.line {
  display: flex;
  text-align: center;
  justify-content: center;
}
.line .el {
  border: none;
  border-radius: 50%;
  background: white;
  width: 7px;
  height: 7px;
  margin: 0 10px;
  position: relative;
  cursor: pointer;
}
.line .el.el_active {
  background: green;
}
.line.icons i {
  color: white;
  background: #2c3b42;
  font-size: 17px;
  padding: 7px;
  border: none;
  border-radius: 50%;
  min-width: 32px;
  min-height: 32px;
  margin-left: 12px;
  transition: 0.3s;
}
.line.icons i:hover {
  background: green;
}

.w7 {
  font-weight: 700;
}

.left {
  text-align: left;
}
.left h1, .left h3, .left h4, .left h5, .left h6, .left p, .left span {
  text-align: left;
}

.mc {
  display: flex !important;
  flex-direction: column !important;
  justify-content: center !important;
}

.df {
  display: flex;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
}

#logo {
  margin-left: 10px;
}

#moon {
  width: 40px;
  height: 40px;
  margin-right: 30px;
  transition: 0.25s;
}
#moon:hover {
  transform: scale(1.2);
}

.wrapper {
  position: relative;
}
.wrapper.dark {
  background: #052825;
  color: white;
}
.wrapper.dark .container-fluid {
  background-color: #052825;
}
.wrapper.dark #logo, .wrapper.dark #moon {
  filter: invert(100%);
}
.wrapper.dark nav a {
  color: white !important;
}
.wrapper.dark .footer hr {
  color: white;
  opacity: 1;
}

nav {
  padding: 20px 0 !important;
}
nav * {
  align-items: center;
}
nav .mr-2 {
  margin-right: 2rem;
}
nav a {
  color: black !important;
  text-decoration: none !important;
}
nav #navbarSupportedContent {
  flex-grow: 0;
}

button {
  transition: 0.25s;
  background: #2E4FD7;
  color: white;
  padding: 15px 25px;
  border: 2px solid #2E4FD7;
}
button.empty {
  color: #2E4FD7;
  background: white;
}
button:hover {
  transform: scale(1.05);
}

input, textarea {
  border: 1px solid #B4B4B4;
  padding: 15px 25px;
}

.container-fluid {
  padding: 80px 0;
}

h1 {
  font-size: 64px;
}

h2 {
  font-size: 54px;
}

h3 {
  font-size: 48px;
  margin-bottom: 60px;
}

.cd.left {
  width: 80%;
}
.cd > * {
  margin-bottom: 4%;
}
.cd .df {
  justify-content: space-between;
}
.cd .df * {
  width: 48%;
}
.cd .brands img {
  margin-right: 4%;
}

.first .content {
  display: grid;
  grid-template-columns: 1.8fr 1fr;
}

.second .container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.second .right > * {
  margin-bottom: 4%;
}
.second .df {
  width: 90%;
}
.second .df img {
  margin-right: 1.8%;
}

.third .df {
  width: 100%;
}
.third .df img {
  margin-right: 3.2%;
  width: 68px;
  height: 68px;
}

.fourth .circle {
  width: 24px;
  height: 24px;
  border: none;
  border-radius: 50%;
  background: #2E4FD7;
  margin-right: 5%;
  margin-bottom: 5%;
}
.fourth .dash {
  width: 70%;
  transform: translate(0, -65%);
  border-bottom: 2.4px dotted #2E4FD7;
}

.fifth {
  padding-bottom: 20px;
  background: #E7EBFC;
  text-align: center;
}
.fifth .brands {
  margin: 0 auto;
  display: flex;
  width: 90%;
  justify-content: space-between;
}
.fifth .container > * {
  margin-bottom: 3.2%;
}

.first-c {
  background: #E7EBFC;
}
.first-c .container {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
}
.first-c .left {
  width: 90%;
}
.first-c .left > * {
  margin-bottom: 30px;
}
.first-c .left .df {
  justify-content: space-between;
  margin-top: 60px;
}
.first-c .left .df button {
  width: 46%;
}
.first-c .left .brands {
  display: flex;
  margin-top: 60px;
}
.first-c .left .brands img {
  margin-right: 35px;
}
.first-c form {
  color: black;
  padding: 36px 30px;
  background: white;
}
.first-c form > * {
  margin: 20px 0;
}
.first-c form .df {
  justify-content: space-between;
}
.first-c form .df input {
  width: 48%;
}
.first-c form textarea, .first-c form button {
  width: 100%;
}

.footer {
  background: #052825;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  bottom: 0;
  width: 100%;
  padding: 0px 40px 80px;
}
.footer hr {
  opacity: 0;
  width: 100%;
  height: 2px;
  margin-bottom: 5%;
}